import React from 'react'
import ConfigPart from './ConfigPart';
import CodeExample from '../CodeExample';

const DocsMeasure = ({ }) => {

    const measureExample = `<div class='measure margin-auto'>
    Lorem ipsum dolor sit amet, consectetur adipisicing elit.
    Aliquid quam facilis, magni dolorum libero nam exercitationem
    sed beatae nobis expedita. Nihil ipsasunt voluptate distinctio.
</div>`

    return (
        <div>

            <div className='margin-bottom-1 measure margin-auto'>
                <h3>Measure</h3>
                <code className='padding-left-0'>
                    <p className='text-small margin-bottom-0'>
                        The .measure className is used to help your visitor to read easily your content.
                        We recommend to adjust the measure to have between 45 and 75 characters per lines.
                        For multiple column, betweend 40 and 50 characters is more suitable.
                    </p>
                </code>
            </div>

            <div className='measure margin-auto'>

                <CodeExample code={measureExample} />

            </div>

            <div className='margin-top-1 code-example' dangerouslySetInnerHTML={{ __html: measureExample }} />


        </div>
    )
}
export default DocsMeasure
