import React from 'react'
import CodeExample from '../CodeExample';

const DocsHero = ({ }) => {


    const heroExample = `\
<section class="hero background-primary">
    <footer class="container">
        Foot
    </footer>
    <div class="measure">
        <p>
        Mon paragraphe
        </p>
    </div>
    <header>
        Head
    </header>
</section>
`

    return (
        <section>

            <h3>Heros</h3>

            <div className='columns'>
                <div className='column'>

                    <CodeExample code={heroExample} />
                </div>
                <div className='column padding-top-1'>

                    <div dangerouslySetInnerHTML={{ __html: heroExample }} />

                </div>
            </div>
        </section>
    )
}
export default DocsHero