import React, { useState } from "react";
import CodeExample from "../CodeExample";
import ConfigPart from "./ConfigPart";

const DocsMargin = () => {

    const [size, setSize] = useState('1');
    const [margin, setMargin] = useState('');

    return (
        <section>
            <div className='columns'>
                <div className='column' style={{ flex: 1 }}>
                    <h3 className='margin-0'>Margin management</h3>

                    <p className='text-small'>The unit used is the line-height.</p>

                    <CodeExample code={`<div class='margin${margin}-${size}'>Hello world</div>`} />

                    <ConfigPart>

                        <div className='margin-top-0-5 stick'>
                            <label>Marging</label>
                            <button style={{ verticalAlign: 'middle' }} className={`small ${margin === '' && 'primary'}`} onClick={() => { setMargin('') }}>Every margin</button>
                            <button style={{ verticalAlign: 'middle' }} className={`small ${margin === '-top' && 'primary'}`} onClick={() => { setMargin('-top') }}>top</button>
                            <button style={{ verticalAlign: 'middle' }} className={`small ${margin === '-right' && 'primary'}`} onClick={() => { setMargin('-right') }}>right</button>
                            <button style={{ verticalAlign: 'middle' }} className={`small ${margin === '-bottom' && 'primary'}`} onClick={() => { setMargin('-bottom') }}>bottom</button>
                            <button style={{ verticalAlign: 'middle' }} className={`small ${margin === '-left' && 'primary'}`} onClick={() => { setMargin('-left') }}>left</button>
                        </div>

                        <div className='margin-top-0-5  stick'>

                            <label>Size</label>

                            <button style={{ verticalAlign: 'middle' }} className={`small ${size === '0' && 'primary'}`} onClick={() => { setSize('0') }}>0</button>
                            <button style={{ verticalAlign: 'middle' }} className={`small ${size === '0-5' && 'primary'}`} onClick={() => { setSize('0-5') }}>0.5</button>
                            <button style={{ verticalAlign: 'middle' }} className={`small ${size === '1' && 'primary'}`} onClick={() => { setSize('1') }}>1</button>
                            <button style={{ verticalAlign: 'middle' }} className={`small ${size === '1-5' && 'primary'}`} onClick={() => { setSize('1-5') }}>1.5</button>
                            <button style={{ verticalAlign: 'middle' }} className={`small ${size === '2' && 'primary'}`} onClick={() => { setSize('2') }}>2</button>
                            <button style={{ verticalAlign: 'middle' }} className={`small ${size === '3' && 'primary'}`} onClick={() => { setSize('3') }}>3</button>
                        </div>

                    </ConfigPart>

                </div>
                <div className='column' style={{ flex: 1, display: 'flex', alignItems: 'center', justifyContent: 'center' }}>

                    <div className='background-primary' style={{ display: 'inline-block' }} dangerouslySetInnerHTML={{ __html: `<div class='background-dark margin${margin}-${size}'>Hello world</div>` }} />

                </div>
            </div>
        </section>
    )
}

export default DocsMargin