import React, { useState } from "react";
import CodeExample from "../CodeExample";
import ConfigPart from "./ConfigPart";

const DocsPadding = () => {

    const [size, setSize] = useState('1');
    const [padding, setPadding] = useState('');

    return (
        <section>

            <div className='columns margin-top-3'>
                <div className='column' style={{ flex: 1 }}>
                    <h3 className='margin-0'>Padding management</h3>

                    <p className='text-small'>The unit used is the line-height.</p>

                    <CodeExample code={`<div class='padding${padding}-${size}'>Hello world</div>`} />

                    <ConfigPart>

                        <div className='margin-top-0-5 stick'>
                            <label>Padding</label>
                            <button className={`small rounded ${padding === '' && 'primary'}`} onClick={() => { setPadding('') }}>Every padding</button>
                            <button className={`small ${padding === '-top' && 'primary'}`} onClick={() => { setPadding('-top') }}>top</button>
                            <button className={`small ${padding === '-right' && 'primary'}`} onClick={() => { setPadding('-right') }}>right</button>
                            <button className={`small ${padding === '-bottom' && 'primary'}`} onClick={() => { setPadding('-bottom') }}>bottom</button>
                            <button className={`small ${padding === '-left' && 'primary'}`} onClick={() => { setPadding('-left') }}>left</button>
                        </div>

                        <div className='margin-top-0-5 stick'>

                            <label>Size</label>
                            <button className={`small ${size === '0' && 'primary'}`} onClick={() => { setSize('0') }}>0</button>
                            <button className={`small ${size === '0-5' && 'primary'}`} onClick={() => { setSize('0-5') }}>0.5</button>
                            <button className={`small ${size === '1' && 'primary'}`} onClick={() => { setSize('1') }}>1</button>
                            <button className={`small ${size === '2' && 'primary'}`} onClick={() => { setSize('2') }}>2</button>
                            <button className={`small ${size === '3' && 'primary'}`} onClick={() => { setSize('3') }}>3</button>
                        </div>
                    </ConfigPart>

                </div>
                <div className='column' style={{ flex: 1, display: 'flex', alignItems: 'center', justifyContent: 'center' }}>

                    <div style={{ display: 'inline-block' }} dangerouslySetInnerHTML={{ __html: `<div class='background-dark padding${padding}-${size}'>Hello world</div>` }} />

                </div>
            </div>
        </section>
    )
}

export default DocsPadding;