import React, { useState } from 'react'
import DocLayout from '../components/layout/doc';
import Layout from "../components/layout"
import CodeExample from '../components/CodeExample';
import CodeFormatContext from '../components/contexts/CodeFormatContext';
import DocsInput from '../components/docs/DocsInput';
import DocsMargin from '../components/docs/DocsMargin';
import DocsPadding from '../components/docs/DocsPadding';
import DocsMeasure from '../components/docs/DocsMeasure';
import DocsText from '../components/docs/DocsText';
import DocsHero from '../components/docs/DocsHero';
import DocsModal from '../components/docs/DocsModal';
import DocsBackground from '../components/docs/DocsBackground';
import DocsContainer from '../components/docs/DocsContainer';
import DocsColumns from '../components/docs/DocsColumns';

const TextSection = () => (
    <section>

        <DocsText />
        <DocsMeasure />

    </section>
)


const DisplaySection = () => {


    return (
        <section>

            <DocsBackground />
            <DocsContainer />
            <DocsColumns />

        </section>
    )
}

const HelpersSection = () => {

    return (
        <>

            <section className='margin-bottom-3'>
                <h3>Responsive</h3>

                <div className='columns'>
                    <div className='column padding-right-0-5'>

                        <CodeExample code="<div class='desktop-only'/>Only on desktop</div>" />
                    </div>
                    <div className='column padding-left-0-5'>
                        <CodeExample code="<div class='mobile-only'/>Only on mobile</div>" />

                    </div>
                </div>

            </section>
            <DocsMargin />
            <DocsPadding />
        </>
    )
}

const ComponentsSection = () => {

    return (
        <>

            <DocsHero />
            <DocsModal />
        </>
    )
}

const FormsSection = () => {

    return (
        <>
            <DocsInput />
        </>
    )
}

const MenuItem = ({ children, onClick, active }) => (

    <div
        onClick={onClick}
        className={`MenuItem box text-center margin-1 hover-grey-3`}
        style={{ flex: 1 }}
    >
        {children}
    </div>
)

export default () => {

    const [modalOpen, setModalOpen] = useState(false)
    const [section, setSection] = useState(
        window.location.hash ? window.location.hash.replace('#', '') : 'text'
    )

    const [format, setFormat] = useState('html')

    const changeFormat = format => {
        setFormat(format)
    }
    return (
        <Layout>
            <div className={`modal ${modalOpen && 'active'}`}>
                <div className='background-white' style={{ height: '90%', width: '90%' }}>
                    Cheat Sheet
                </div>
            </div>

            <div className='container margin-top-1 margin-bottom-1'>

                <div style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'center' }}>

                    <MenuItem onClick={() => { setSection('text') }} active={true}>
                        <a href="#text">
                            <div className={`height-2 ${section === 'text' && 'color-primary'}`}>
                                <i className='fas fa-paragraph fa-2x' />
                            </div>
                            <small className={`${section === 'text' && 'color-primary'}`}>Text</small>
                        </a>
                    </MenuItem>

                    <MenuItem onClick={() => { setSection('display') }}>
                        <a href="#display">
                            <div className={`height-2 ${section === 'display' && 'color-primary'}`}>
                                <i className='fas fa-th-large fa-2x' />
                            </div>
                            <small className={`${section === 'display' && 'color-primary'}`}>Display</small>
                        </a>
                    </MenuItem>

                    <MenuItem onClick={() => { setSection('components') }}>
                        <a href="#components">

                            <div className={`height-2 ${section === 'components' && 'color-primary'}`}>
                                <i className='fas fa-cog fa-2x' />
                            </div>
                            <small className={`${section === 'components' && 'color-primary'}`}>Components</small>
                        </a>
                    </MenuItem>

                    <MenuItem onClick={() => { setSection('helpers') }}>
                        <a href="#helpers">
                            <div className={`height-2 ${section === 'helpers' && 'color-primary'}`}>
                                <i className='fas fa-tools fa-2x' />
                            </div>
                            <small className={`${section === 'helpers' && 'color-primary'}`}>Helpers</small>
                        </a>
                    </MenuItem>

                    <MenuItem onClick={() => { setSection('forms') }}>
                        <a href="#forms">
                            <div className={`height-2 ${section === 'forms' && 'color-primary'}`}>
                                <i className='fas fa-bars fa-2x' />
                            </div>
                            <small className={`${section === 'forms' && 'color-primary'}`}>Forms</small>
                        </a>
                    </MenuItem>

                </div>

                <hr />

                <CodeFormatContext.Provider value={{
                    format, setFormat: changeFormat
                }}>
                    {
                        section === 'text' ? <TextSection /> :
                            section === 'display' ?
                                <DisplaySection />
                                : section === 'helpers' ?
                                    <HelpersSection />
                                    : section === 'components' ?
                                        <ComponentsSection /> :
                                        <FormsSection />
                    }
                </CodeFormatContext.Provider>
            </div>
        </Layout>
    )
}