import React from 'react'

const ConfigPart = ({ children }) => {
    return (
        <div
            className='padding-0-5 ConfigPart'
            style={{ backgroundColor: '#dedede', position: 'relative', borderRadius: '0 0 4px 4px' }}>

            <i className='fas fa-cog ' style={{ position: 'absolute', right: 10, top: 10, opacity: 0.3 }} />

            {children}
        </div>
    )
}
export default ConfigPart