import React, { useState } from 'react'
import CodeExample from '../CodeExample';
import ConfigPart from './ConfigPart';

const DocsColumns = ({ }) => {

    const [count, setCount] = useState(2)

    const example = `\
<div class='columns'>
${(new Array(count)).fill(`    <div class='column'>Lorem ipsum</div>`).join('\n')}
</div>\
${(new Array(4 - count)).fill(' \n').join('')}\
            `

    return (
        <section>

            <h3>Columns</h3>

            <div className='columns'>
                <div className='column'>

                    <CodeExample code={example} />
                    <ConfigPart>

                        <div className='stick margin-top-0-5'>
                            <label>Color</label>

                            <button className={`small ${count === 1 ? 'primary' : ''
                                }`} onClick={() => { setCount(1) }}>1</button>
                            <button className={`small ${count === 2 ? 'primary' : ''} `} onClick={() => { setCount(2) }}>2</button>
                            <button className={`small ${count === 3 ? 'primary' : ''} `} onClick={() => { setCount(3) }}>3</button>
                            <button className={`small ${count === 4 ? 'primary' : ''} `} onClick={() => { setCount(4) }}>4</button>

                            {/* <button className={`small ${ color === '' ? 'primary' : '' } `} onClick={() => { setColor('') }}>normal</button>
                            <button className={`small ${ color === 'primary' ? 'primary' : '' } `} onClick={() => { setColor('primary') }}>primary</button>
                            <button className={`small ${ color === 'info' ? 'primary' : '' } `} onClick={() => { setColor('info') }}>info</button>
                            <button className={`small ${ color === 'warning' ? 'primary' : '' } `} onClick={() => { setColor('warning') }}>warning</button>
                            <button className={`small ${ color === 'danger' ? 'primary' : '' } `} onClick={() => { setColor('danger') }}>danger</button> */}
                        </div>
                    </ConfigPart>

                </div>
                <div className='column code-example'>

                    <div dangerouslySetInnerHTML={{ __html: example }} />
                </div>
            </div>

        </section>
    )
}
export default DocsColumns