import React, { useState } from 'react'
import CodeExample from '../CodeExample';
import ConfigPart from './ConfigPart';

const DocsInput = () => {

    const [size, setSize] = useState('medium')
    const [fullwidth, setFullWidth] = useState(false)
    const [color, setColor] = useState('primary')
    const [rounded, setRounded] = useState(false)

    const inputSizeExemple = `\
<input
    value='mbote na yo'
    class='${
        (size + ' ' + (fullwidth ? 'fullwidth' : '') + ' ' + color + ' ' + (rounded ? 'rounded' : '')
        ).trim()}'
    />
`

    return (
        <section>

            <h3 className='margin-0'>Input</h3>

            <div className='columns'>
                <div className='column'>

                    <CodeExample code={inputSizeExemple} />

                    <ConfigPart>

                        <div className='stick'>
                            <label>Size</label>
                            <button className={`small ${size === 'small' ? 'primary' : ''}`} onClick={() => { setSize('small') }}>small</button>
                            <button className={`small ${size === 'normal' ? 'primary' : ''}`} onClick={() => { setSize('normal') }}>normal</button>
                            <button className={`small ${size === 'medium' ? 'primary' : ''}`} onClick={() => { setSize('medium') }}>medium</button>
                            <button className={`small ${size === 'big' ? 'primary' : ''}`} onClick={() => { setSize('big') }}>big</button>
                        </div>

                        <div className='stick margin-top-0-5'>
                            <label>Color</label>
                            <button className={`small ${color === '' ? 'primary' : ''}`} onClick={() => { setColor('') }}>normal</button>
                            <button className={`small ${color === 'primary' ? 'primary' : ''}`} onClick={() => { setColor('primary') }}>primary</button>
                            <button className={`small ${color === 'info' ? 'primary' : ''}`} onClick={() => { setColor('info') }}>info</button>
                            <button className={`small ${color === 'warning' ? 'primary' : ''}`} onClick={() => { setColor('warning') }}>warning</button>
                            <button className={`small ${color === 'danger' ? 'primary' : ''}`} onClick={() => { setColor('danger') }}>danger</button>
                        </div>

                        <div className='margin-top-0-5'>
                            <label className='text-small'>
                                <input type='checkbox' checked={fullwidth} onClick={() => { setFullWidth(!fullwidth) }} />
                                {' '}
                                fullwidth
                            </label>
                            {' '}
                            <label className='text-small'>
                                <input type='checkbox' checked={rounded} onClick={() => { setRounded(!rounded) }} />
                                {' '}
                                rounded
                            </label>
                        </div>

                    </ConfigPart>


                </div>
                <div className='column padding-left-1 padding-top-1'>
                    <div dangerouslySetInnerHTML={{ __html: inputSizeExemple }} />
                </div>
            </div>

        </section>
    )
}

export default DocsInput;